/* Shadows and Gradients */
body {
  color: #000;
  background-image: -webkit-linear-gradient(45deg, #ffffff 25%, #fafafa 25%, #fafafa 50%, #ffffff 50%, #ffffff 75%, #fafafa 75%, #fafafa 100%);
  background-image: linear-gradient(45deg, #ffffff 25%, #fafafa 25%, #fafafa 50%, #ffffff 50%, #ffffff 75%, #fafafa 75%, #fafafa 100%);
  background-size: 20.00px 20.00px; }

.App {
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 16px;
  margin: 0 auto;
  max-width: 1024px; }

h1, h2, h3 {
  text-align: center; }

h2, h3 {
  font-size: 16px; }

.list-options {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 30px auto 10px auto; }
  @media only screen and (min-width: 768px) {
    .list-options {
      text-align: center;
      -webkit-flex-direction: row;
              flex-direction: row;
      -webkit-justify-content: center;
              justify-content: center; } }
  .list-options-label {
    font-weight: 600;
    margin-right: 10px; }
    @media only screen and (min-width: 768px) {
      .list-options-label__second {
        margin-left: 20px; } }

.list-options-label,
.input-wrapper {
  margin-bottom: 10px; }
  @media only screen and (min-width: 768px) {
    .list-options-label,
    .input-wrapper {
      margin-bottom: 0; } }

.input-wrapper {
  display: inline;
  margin-right: 10px; }

.language-option {
  max-width: 200px; }

.data-table {
  width: 100%;
  border: 2px solid gray;
  border-collapse: collapse;
  background-color: white;
  margin-bottom: 50px;
  overflow: hidden; }

.data-table-head {
  background-color: #d1d1d1;
  height: 40px;
  text-align: left; }

.row {
  height: 30px;
  border: 1px solid #f2f2f2; }
  .row:hover {
    box-shadow: 0px 0px 23px -4px rgba(0, 0, 0, 0.75); }

.td-no {
  text-align: center; }

.td-language {
  font-family: 'Source Code Pro'; }

.td-link a {
  text-decoration: none; }

.td-language-dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-right: 10px; }

.td-author {
  display: none; }
  @media only screen and (min-width: 768px) {
    .td-author {
      display: table-cell; } }

.td-stars,
.td-forks {
  display: none; }
  @media only screen and (min-width: 1024px) {
    .td-stars,
    .td-forks {
      display: table-cell; } }

.dot-loader {
  position: relative;
  overflow: hidden;
  height: 10px;
  width: 100%; }
  .dot-loader .dots {
    position: absolute;
    left: 0;
    top: 0;
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: red;
    -webkit-animation: dot-move 2s infinite cubic-bezier(0.2, 0.64, 0.81, 0.23);
            animation: dot-move 2s infinite cubic-bezier(0.2, 0.64, 0.81, 0.23); }
    .dot-loader .dots:nth-child(2) {
      -webkit-animation-delay: 150ms;
              animation-delay: 150ms; }
    .dot-loader .dots:nth-child(3) {
      -webkit-animation-delay: 300ms;
              animation-delay: 300ms; }
    .dot-loader .dots:nth-child(4) {
      -webkit-animation-delay: 450ms;
              animation-delay: 450ms; }

@-webkit-keyframes dot-move {
  0% {
    left: 0%; }
  75% {
    left: 100%; }
  100% {
    left: 110%; } }

@keyframes dot-move {
  0% {
    left: 0%; }
  75% {
    left: 100%; }
  100% {
    left: 110%; } }

